@font-face {
  font-family: 'opensans';
  src: url('../fonts/Open-sans/opensans-regular-webfont.woff2') format('woff2'),
       url('../fonts/Open-sans/opensans-bold-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Hanken book';
  src: url('https://daks2k3a4ib2z.cloudfront.net/590d9229b4700605d36b8ed3/590d9229b4700605d36b8f1c_Hanken-Book.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Hanken';
  src: url('https://daks2k3a4ib2z.cloudfront.net/590d9229b4700605d36b8ed3/590d9229b4700605d36b8f25_Hanken-Light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
/* font: Hanken book */
*{
  box-sizing: border-box;
  text-align: center;
  margin: 0;
  padding: 0;
}
html{
  font-size: 62.5%;
}
body {
  /* background-color: #fcd750; */
  font-family: 'opensans';
  margin: 0;
  font-size: 1rem;
  line-height: 1.5em;
  color: #2d2c4c;
}