/* NavBar Styles */
.nav-content{
   position: fixed;
   width: 100%;
   padding: 1.6rem;;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   z-index: 1;
   background: #fcd750;
}
.flex a{
    height: 25px;
}
.logo{
    object-fit: cover;
    max-width: 100%;
    width: 130px;
}
.hamburger{
   background: transparent;
    z-index: 1;
    position: relative;
    display: block;
    width: 26px;
    cursor: pointer;
    appearance: none;
    outline: none;
    border: none;
 }
.hamburger .bar, .hamburger::after, .hamburger::before{
    content: '';
    display: block;
    width: 100%;
    height: 4px;
    background-color: #2d2c4c;
    margin: 4px 0px;
    transition: 0.4s;
    border-radius: 5px;
 }
 .hamburger.is-active::before{
    transform: rotate(-45deg) translate(-6px, 7px);
    background: #0082f3;
 }
 .hamburger.is-active::after{
    transform: rotate(45deg) translate(-4px, -6px);
    background: #0082f3;
 }
 .hamburger.is-active .bar{
    opacity: 0;
 }

/* Mobile-Nav Styles */
.mobile-nav{
   position: fixed;
   top: -300px;
   width: 100%;
   display: block;
   z-index: 0;
   background-color: #fcd750;
   border-bottom: 2px solid #2d2c4c;
   transition: 0.4s;
}
 .nav-link{
  color: black;
  font-size: 2.2rem;
  display: block;
  width: 100%;
  text-decoration: none;
  text-transform: capitalize;
  line-height: 1.1em;
  padding: 2.4rem 0;
  padding-left: 3.2rem;
  text-align: left;
}
.nav-link:active{
   border-bottom: 2px solid #2d2c4c;
}
.mobile-nav.is-active{
   top: 60px;
}