/* Hero Section*/
.hero{
  padding-top: 8rem !important;
  background-color: var(--bg-primary);
  text-align: center;
  padding-bottom: 3.2rem;
  /* color: var(--text-primary); */
}
.hero-message{
  margin-bottom: 2.4rem;
}
.subtitle-hero{
  margin-bottom: 4.8rem;
}
#hero-img{
  display: inline-block;
  width: 92%;
  margin-bottom: 3.2rem;
}
.item{
  margin-bottom: 2.4rem;
}
.item-btn{
  display: inline-block;
  color: var(--text-secondary);
  font-family: "Hanken book";
  border-radius: 3px;
  font-size: 1rem;
  background-color: var(--bg-button);
  padding: 1.4rem 1.3rem 1.1rem;
  letter-spacing: 0.1em;
}
  /* Process Section */
  .process{
    background-color: white;
    color: #2d2c4c;
  }

  /* Features Section */
  .features{
    background-color: #bce3f5;
    color: #2d2c4c;
  }
  .ft-item{
    padding: 0 1.6rem;
    margin-bottom: 3.2rem;
  }
  .ft-img{
    display: inline-block; /* Fix Later */
    width: auto;
    margin-bottom: 3.2rem;
  }

  /* Services Section */
  .services{
    background-color: #f2f2f2;
  }

  /* About Section */
  .about{
    background-color: #fcd750;
    color: #2d2c4c;
    padding: 3.2rem 0;
    text-align: center;
  }
  .about .container{
    margin: 0 3.2rem;
  }
  .about .btn{
    font-size: 1.4rem;
    background-color: #2d2c4c;
  }

  /* How Section */
  .how{
    font-size: 1.4rem;
    background-color: #f2f2f2;
  }
  .how .subtitle{
    margin-bottom: 0;
  }
  .how .btn{
    font-size: 1.4rem;
    background-color: #20a5e6;
  }