/* cta: container ft: feature*/
/* Variables */
:root{
    --bg-primary: #fcd750;
    --bg-secondary: #f2f2f2;
    --bg-tertiary: #bce3f5;
    --text-primary: #2d2c4c;
    --text-secondary: #ffffff;
    --text-tertiary: #21a4e6;
    --bg-button: #3697ec;
}

/* Background Styles */
.primary{
    background: var(--bg-primary);
}
.white-grey{
    background: var(--bg-secondary);
}
.light-blue{
    background: var(--bg-tertiary);
}

/* Logo-Hover Animation */
@keyframes iconhover {
    0%{
      transform: scale(1);
    }
    25%{
      transform: scale(1.125);
    }
    50%{
      transform: scale(1.25);
    }
    100%{
      transform: scale(1);
    }
  }
/* General Styles */
a{
    font-family: 'Hanken book';
    text-decoration: none;
    cursor: pointer;
    display: inline-block;
    text-transform: uppercase;
    color: var(--text-primary);
}
img{
    /* display: inline-block; */
    max-width: 100%;
    vertical-align: middle;
}
h1{
    font-family: "Hanken book";
    font-size: 4.1rem;
    text-align: center;
    margin-bottom: 0.8rem;
    font-weight: 400;
    line-height: 1.1em;
    text-transform: uppercase;
}
h2{
    font-family: "Hanken book";
    font-size: 3.6rem;
    text-transform: uppercase;
    line-height: 1.1em;
    font-weight: 400;
    color: var(--text-primary);
}
h3{
    font-family: "Hanken book";
    font-size: 2.6rem;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 1.6rem;
    line-height: 1.3em;
}
h4{
    font-family: "Hanken book";
    font-size: 2.2rem;
    margin-bottom: 1.6rem;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.2em;
    letter-spacing: 0.03em;
}
p{
    color: var(--text-primary);
    font-size: 1.6rem;
    line-height: 1.5em;
    margin-bottom: 3.2rem;
    letter-spacing: -0.01em;
}
p::first-letter{
    text-transform: capitalize;
}
button{
    font-family: "Hanken book";
    display: inline-block;
    border-radius: 4px;
    background-color: var(--bg-button);
    color: var(--text-secondary);
    border: 0;
    cursor: pointer;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}
ul{
    list-style: none;
    text-align: center;
}

/* Grouped Styles */
.section{
    padding-top: 4.8rem;
    padding-bottom: 3.2rem;
    line-height: 1.5em;
}
.label{
    display: inline-block;
    font-size: 1.4rem;
    line-height: 1.7em;
    background-color: var(--text-primary);
    color: var(--text-secondary);
    margin-bottom: 3.2rem;
    padding: 0 0.8rem;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}
.title{
    font-family: "Hanken book";
    font-size: 1.6rem;
    color: var(--text-tertiary);
    line-height: 1.2em;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    /* padding-bottom: 0.5rem; */
}
.container{
    padding: 0 1.6rem;
}
.section-header{
    margin-bottom: 3.2rem;
}
.subtitle{
    font-size: 1.6rem;
    font-weight: 600;
    line-height: 1.5em;
    color: #2d2c4c;
}
.subtitle::first-letter{
    text-transform: capitalize;
}
.item-img{
    display: inline-block;
    height: 66px;
    margin-bottom: 3.2rem;
}
.cta-icon{
    display: inline-block;
    width: 23%;
    margin-bottom: 3.2rem;
}
.btn{
    padding: 2rem 2.4rem 1.6rem;
    border-radius: 3px;
    letter-spacing: 0.07em;
    color: var(--text-secondary);
}
.link{
    font-family: 'opensans';
    text-transform: none;
    display: block;
}
/* Footer Section */
.footer{
    padding: 3.2rem 0;
    background-color: #2d2c4c;
}
.link-footer{
    color: var(--bg-primary);
}
.content{
    font-size: 1.6rem;
    line-height: 1.4em;
    margin-bottom: 2.4rem;
    color: #fff;
}
.content-txt{
    color: var(--text-secondary);
    font-size: 1.6rem;
    margin-bottom: 1.6rem;
}
#content-title{
    font-size: 1.6rem;
    margin-bottom: 0.8rem;
}
.link-social{
    display: inline-block;
    width: 11%;
    margin: 0 0.8rem;
    /* animation: iconhover 4s; */
}
.foot{
    margin-bottom: 0;
}